import React, { Component } from 'react';
import './index.css';
import LngSupport from '../Lng/LngSupport.js';
import Lng from '../Lng/Lng.js';
import Header from '../Header';
import Footer from '../Footer';
import AboutMe from '../AboutMe';
import SocialHub from '../SocialHub';
import Project from '../Project';
import FeedbackForm from '../FeedbackForm';
import Message from '../Message';
import imgASC from './assets/ASC_ICON.webp';
import imgERI from './assets/ERI_icon.webp';
import imgXDA from './assets/xda-flat-circle-icon-256.webp';
import imgReactJS from './assets/Reactlogo512.png';
import imgReactMultyLng from './assets/ReactlogoMultyLng.png';
import imgReactYTOptLib from './assets/ReactlogoYTOptLib.png';

export default class Main extends Component {

    state = {
        LngDefault: "en",
        LngCurrent: "en",
        LngShowMenu: true,
        MessageShow: false,
        MessageText: "",
    };

    lngDetect = (MainState) => {
        if (!MainState.hasOwnProperty("UserLng")) {
            MainState.UserLng = navigator.language.substr(0, 2) || navigator.userLanguage.substr(0, 2);
            if (LngSupport.hasOwnProperty(MainState.UserLng)) {
                if (LngSupport[MainState.UserLng].autouse) {
                    MainState.LngCurrent = MainState.UserLng;
                };
            };
        };
    };

    lngLoad = (MainState) => {
        if (Lng.hasOwnProperty(MainState.LngDefault)) {
            if (Lng.hasOwnProperty(MainState.LngCurrent)) {
                MainState.Lng = Object.assign({}, Lng[MainState.LngDefault], Lng[MainState.LngCurrent]);
                document.documentElement.lang = MainState.LngCurrent;
            } else {console.log("Main-lngLoad()", " : '"+MainState.LngCurrent+" (LngCurrent)' not found in Lng.js!")};
        } else {console.log("Main-lngLoad()", " : '"+MainState.LngDefault+" (LngDefault)' not found in Lng.js!")};     
    };

    MainSetState = (value) => {
        this.setState(value);
    };

    render() {
        let MainState = this.state;
        this.lngDetect(MainState);
        this.lngLoad(MainState);
        return(
            <div className="main">
                <Header MainState={MainState} SetState={this.MainSetState}/>
                <AboutMe MainState={MainState} />
                <SocialHub lng={MainState.Lng} />
                <div className="portf-text"><span>{MainState.Lng.MainTextMyWorks}</span></div>
                <Project img={imgASC}
                        prj_text={MainState.Lng.ProjectASCText} 
                        ablock={true}/>
                <Project img={imgERI} 
                        prj_text={MainState.Lng.ProjectERIText}/>
                <Project img={imgXDA}
                        prj_text={MainState.Lng.ProjectXDAText}
                        ablock={true}/>
                <Project img={imgReactMultyLng}
                        prj_text={MainState.Lng.ProjectReactMultyLngText}
                        ablock={false}/>
                <Project img={imgReactYTOptLib}
                         prj_text={MainState.Lng.ProjectReactYTLib}
                         ablock={true}/>
                <Project img={imgReactJS}
                        prj_text={MainState.Lng.ProjectReactSiteText}
                        ablock={false} />
                <FeedbackForm MainState={MainState} MainSetState={this.MainSetState}/>
                <Footer MainState={MainState} />
                {MainState.MessageShow &&
                    <Message MainState={MainState} MainSetState={this.MainSetState}/>}
            </div>
        );
    };
};