import React, { Component } from 'react';
import LngSupport from '../Lng/LngSupport.js';
import './index.css';

export default class Header extends Component {

    render() {
        const { MainState } = this.props;
        const LngOptions = [];

        for (var key in LngSupport) {
            LngOptions.push(
                <option className="header-lang-option" key={LngSupport[key].id} disabled={LngSupport[key].disabled} hidden={LngSupport[key].hidden} value={key}>{LngSupport[key].name}</option>
            );
        }

        return(
            <div className="header">
                  <div className="header-title-div"> 
                      <span className="header-title-text">Octanium Software</span>
                  </div>
                    {MainState.LngShowMenu &&
                    <div className="header-lang-menu">
                        <span className="header-lang-span">{MainState.Lng.HeaderLanguage}</span>
                        <select className="header-lang-selector" aria-label="Language switcher" defaultValue={MainState.LngCurrent} onChange={(event) => {this.props.SetState({LngCurrent: event.target.value,})}} size="1">
                            {LngOptions}
                        </select>
                    </div>}
            </div>
        );
    };
};