import React, { Component } from 'react';
import './index.css';

export default class Message extends Component {
    
    render() {
        const { MainState , MainSetState} = this.props;

        return(
            <div className="Message">
                <div className="Message-body">
                    <p className="Message-text">{MainState.MessageText}</p>
                    <button type="button" className="btn btn-light Message-btn" onClick={() => {MainSetState({MessageShow: false})}}>{MainState.Lng.MessageBTNOK}</button>
                </div>
            </div>
        );
    };
};