import React, { Component } from 'react';
import './index.css';

export default class SocialHub extends Component {

    render() {

        const { lng = {} } = this.props;

        return(
            <div className="SocialHub">
                <a href="https://octanium-os.blogspot.com" rel="noopener noreferrer" aria-label={lng.SocialHubLinkToBlog}  className="fa fa-pencil social-icon" target="_blank" title={lng.SocialHubBlogTitle} > </a>
                <a href="https://t.me/Octanium" rel="noopener noreferrer " aria-label={lng.SocialHubLinkToTelegram} className="fa fa-telegram social-icon" target="_blank" title={lng.SocialHubTelegramTitle} > </a>
                <a href="https://github.com/Octanium91" rel="noopener noreferrer" aria-label={lng.SocialHubLinkToGithub} className="fa fa-github social-icon" target="_blank" title={lng.SocialHubGithubTitle} > </a>
                <a href="https://www.instagram.com/octanium91/" rel="noopener noreferrer" aria-label={lng.SocialHubLinkToInstagram} className="fa fa-instagram social-icon" target="_blank" title={lng.SocialHubInstagramTitle} > </a>
                <a href="https://www.facebook.com/Octanium91" rel="noopener noreferrer" aria-label={lng.SocialHubLinkToFacebook} className="fa fa-facebook social-icon" target="_blank" title={lng.SocialHubFacebookTitle} > </a>
                <a href="https://www.youtube.com/octanium91" rel="noopener noreferrer" aria-label={lng.SocialHubLinkToYoutube} className="fa fa-youtube-play social-icon" target="_blank" title={lng.SocialHubYoutubeTitle} > </a>
            </div>
        );
    };
};