import React, { Component } from 'react';
import './index.css';

export default class FeedbackForm extends Component {

    state = {
        perentMainstate: "",
    }

    api_telegram_send = (message, MainState, MainSetState) => {
        // use: 
        //  command: api_telegram_send("Hello!, my phone: XXXXXX");
        //  send in telegram:
        //      Hello, my phone: XXXXXX 
        //
        //  OR (if you need segue on new lines)
        //
        //  command: api_telegram_send(["Hello!", "my phone: XXXXXX"]);
        //  send in telegram:
        //      Hello
        //      my phone: XXXXXX
        var text 
        var TextBold = [true, true];
        try {
            text = "<b>Host: "+window.location.hostname+"</b>%0A";
        } catch {
            text = "";
        }
        if (typeof message === "object") {
            var booleanBoTextBold
            message.forEach(function(item, i, message) {
                try {booleanBoTextBold = TextBold[i];} catch {booleanBoTextBold = false;}
                if (booleanBoTextBold) {
                    text = text+"<b>"+item+"</b>%0A";
                } else {
                    text = text+item+"%0A";
                };
            });
        };
        if (typeof message === "string") {
            text = text+message;
        };
        if (process.env.REACT_APP_TELEGRAM_TOKEN === undefined) {
            console.warn("env.REACT_APP_TELEGRAM_TOKEN not found!");
        };
        if (process.env.REACT_APP_TELEGRAM_CHAT === undefined) {
            console.warn("env.REACT_APP_TELEGRAM_CHAT not found!");
        };
        if (text !== "") {
            (async () => {
                try {
                    const response = await fetch("https://api.telegram.org/bot"+process.env.REACT_APP_TELEGRAM_TOKEN+"/sendMessage?chat_id="+process.env.REACT_APP_TELEGRAM_CHAT+"&parse_mode=html&text="+text, { 
                        method: 'POST',});
                    if (response.ok) {
                        // ok
                        MainSetState({  MessageShow: true,
                                        MessageText: MainState.Lng.MessageTextTelegramOK,});
                    } else {
                        console.log("api_telegram_send_error:", response.status);
                        MainSetState({  MessageShow: true,
                            MessageText: MainState.Lng.MessageTextTelegramERROR,});
                    }
                } catch (error) {
                    console.log("api_telegram_send_error:", error);
                    MainSetState({  MessageShow: true,
                        MessageText: MainState.Lng.MessageTextTelegramERROR,});
                }
                document.getElementById("btn_submit").value = MainState.Lng.FeedbackFormbtnSend;
                document.getElementById("btn_submit").disabled = false;
            })();
        } else {
            console.log("api_telegram_send_error:", "'message' is empty! use type: object or string.");
            MainSetState({  MessageShow: true,
                MessageText: MainState.Lng.MessageTextTelegramERROR,});
        };
    };

    SendMess = (MainState, MainSetState) => {
        const input_name = document.getElementById("input_name");
        const input_telephone = document.getElementById("input_telephone");
        const input_message = document.getElementById("input_message");
        var validae_send = true;
        let input_telephone_num = "";
        if (input_name.value === "") {
            validae_send = false;
        }
        if (input_telephone.value === "") {
            validae_send = false;
        } else {
            if (input_telephone.value.charAt(0) === "+") {
                input_telephone_num = "%2B"+input_telephone.value.substr(1);
            } else {
                input_telephone_num = "%2B"+input_telephone.value;
            };
        }
        if (input_message.value === "") {
            validae_send = false;    
        }
        if (validae_send) {
            document.getElementById("btn_submit").disabled = true;
            document.getElementById("btn_submit").value = MainState.Lng.FeedbackFormbtnSending;
            this.api_telegram_send([input_name.value, input_telephone_num, input_message.value], MainState, MainSetState);
            input_name.value = "";
            input_telephone.value = "";
            input_message.value = "";
        };
    };

    TelOnChange = (e, MainState) => {
        const re = /[^+0-9]/g;
        // e.target.value === '' ||
        if (re.test(e.target.value)) {
            document.getElementById("input_telephone_error").innerHTML=MainState.Lng.FeedbackFormFieldTelOnlyNum;
        } else {
            document.getElementById("input_telephone_error").innerHTML="";
        }

        document.getElementById("input_telephone").value = e.target.value.replace(/[^+0-9]/g,'');
    };

    render() {
        const { MainState, MainSetState } = this.props;
        // this.state.perentMainstate = MainState;
        return(
            <div className="feedback-form">
                <div className="feedback-form-form">
                    <p className="feedback-form-text-title">{MainState.Lng.FeedbackFormTextTitle}</p>
                    <form>
                        <div className="input-group mb-3 feedback-form-divs">
                            <div className="input-group-prepend">
                                <span className="input-group-text" id="inputGroup-sizing-default">{MainState.Lng.FeedbackFormFieldUser}</span>
                            </div>
                            <input id="input_name" type="text" className="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" required></input>
                        </div>
                        <p className="feedback-form-input-error" id="input_telephone_error"></p>
                        <div className="input-group mb-3 feedback-form-divs">
                            <div className="input-group-prepend">
                                <span className="input-group-text" id="inputGroup-sizing-default">{MainState.Lng.FeedbackFormFieldTel}</span>
                            </div>
                            <input id="input_telephone" type="tel" pattern="[0-9]*" onChange={(e) => {this.TelOnChange(e, MainState)}} placeholder="+123456789101" className="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" required></input>
                        </div> 
                        <div className="input-group mb-3 feedback-form-divs">
                            <div className="input-group-prepend">
                                <span className="input-group-text" id="inputGroup-sizing-default">{MainState.Lng.FeedbackFormFieldMess}</span>
                            </div>
                            <input id="input_message" type="text" className="form-control" aria-label="Default" aria-describedby="inputGroup-sizing-default" required></input>
                        </div> 
                        <input id="btn_submit" className="btn btn-light" type="submit" onClick={() => {this.SendMess(MainState, MainSetState)}} value={MainState.Lng.FeedbackFormbtnSend}></input>
                    </form>
                </div>
            </div>
        );
    };
};